import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, inputs, charBCs }) => (
      <>
        When <LGreen>Normal Attack DMG</LGreen> is dealt to opponents, Bonus DMG
        will be dealt based on Yun Jin's <LGreen>current DEF</LGreen>.{" "}
        {!toSelf && (
          <Red>DMG Bonus: {buffs[0].bnValue(...inputs, charBCs)}.</Red>
        )}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Current DEF", "Elemental Burst Level"],
    inputs: [0, 1],
    inputTypes: ["text", "text"],
    maxs: [9999, 13],
    addFinalBnes: ({ toSelf, char, charBCs, ATTRs, bonuses, inputs }) => {
      const { bnValue } = buffs[0];
      bonuses.NA.Flat += toSelf
        ? bnValue(
            char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0),
            ATTRs.DEF,
            charBCs
          )
        : bnValue(...inputs, charBCs);
    },
    bnValue: (DEF, level, BCs) => {
      const mult = 32.16 * TalentLvMults[2][level] + buffs[0].getXtraMult(BCs);
      return Math.round((DEF * mult) / 100);
    },
    getXtraMult: (BCs) => {
      let result = 0;
      if (BCs[1].activated) {
        const input = BCs[1].inputs[0];
        result += input * 2.5;
        if (input === 4) result += 1.5;
      }
      return result;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        The <LGreen>Normal Attack DMG Bonus</LGreen> granted by Flying Cloud
        Flag Formation is further increased by <Green>2.5/5/7.5/11.5%</Green> of
        Yun Jin's <LGreen>DEF</LGreen> when the party contains characters of
        1/2/3/4 Elemental Types.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    selfLabels: ["Number of Elemental Types"],
    labels: ["Number of Elemental Types"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4]
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        After Cliffbreaker's Banner is unleashed, all nearby party members's{" "}
        <LGreen>Normal Attack DMG</LGreen> is increased by <Green>15%</Green>{" "}
        for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 10;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When Yun Jin trigger the Crystallize Reaction, her <LGreen>DEF</LGreen>{" "}
        is increased by <Green>20%</Green> for 12s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 20;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Characters under the effects of the Flying Cloud Flag Formation have
        their <LGreen>Normal ATK SPD</LGreen> increased by <Green>12%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 12;
    }
  }
];
