import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, inputs, ATTRs, charBCs }) => {
      const { bnValue } = buffs[0];
      let result;
      if (toSelf) {
        result = bnValue(charBCs[1].activated, ATTRs["Energy Recharge"]);
        if (charBCs[0].activated && charBCs[1].activated) {
          result = Math.round(result * 9.1) / 10;
        }
      } else {
        bnValue(...inputs);
      }
      return (
        <>
          Increases <Green>20%</Green> <LGreen>Energy Recharge</LGreen> during
          the Abundance Amulet's duration. <Red>Total bonus: {result}%</Red>.
        </>
      );
    },
    isGranted: () => true,
    affect: "party",
    labels: ["A4 Passive Talent", "Energy Recharge"],
    inputs: [false, 100],
    inputTypes: ["check", "text"],
    maxs: [null, 999],
    addBnes: ({ ATTRs, inputs, toSelf, charBCs }) => {
      const { bnValue } = buffs[0];
      ATTRs["Energy Recharge"] += toSelf
        ? bnValue(charBCs[1].activated, ATTRs["Energy Recharge"])
        : bnValue(...inputs);
    },
    bnValue: (boosted, ER) => 20 + (boosted ? Math.round(ER) / 10 : 0)
  },
  {
    src: "A4 Passive Talent",
    desc: () => (
      <>
        Increases the <LGreen>Energy Recharge</LGreen> effect granted by
        Lightning Blade's Abundance Amulet by <Green>10%</Green> of the
        Traveler's <LGreen>Energy Recharge</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When Falling Thunder hits an opponent, it will decrease their{" "}
        <LGreen>Electro RES</LGreen> by <Green>15%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult["Electro RES"] -= 15;
    }
  }
];
