import styled from "styled-components";
import { useContext } from "react";
import { TargetCt, SetupCt, ResultCt, FixedCts } from "../index";
import { Text, Link, ColoredSpan } from "../../../styledCpns/DataDisplay";
import characters from "../../../data/characters";
import { colorCodes } from "../../../data/misc";
import { deepCopy } from "../../../helpers/misc";

const Input = styled.input`
  padding: 0.5rem 0.5rem 0.25rem;
  width: 5rem;
  border: none;
  border-radius: 0.25rem;
  text-align: right;
  font-size: 1.125rem;
  &:focus {
    box-shadow: 0 0 0 4px ${colorCodes.red};
  }
`;

const destructedTypes = {
  NA: "Normal Attacks",
  CA: "Charged Attacks",
  AS: "Aimed Shot",
  CAS: "Fully-charged Aimed Shot",
  PA: "Plunging Attacks"
};

export default function Target({ char }) {
  const target = useContext(TargetCt);
  const curSetupI = useContext(SetupCt);
  const { allInfusion } = useContext(ResultCt);
  const { setTarget } = useContext(FixedCts);

  const charData = characters[char.name];
  const { vision, weapon } = charData;
  const notes =
    weapon === "catalyst"
      ? [
          ["NA", vision],
          ["CA", vision],
          ["PA", vision]
        ]
      : Object.entries(allInfusion[curSetupI]);
  if (weapon === "bow") {
    notes[1][0] = "AS";
    notes.splice(2, 0, ["CAS", vision]);
  }
  return (
    <div className="pl-2">
      <div className="pt-2 pr-4">
        {Object.entries(target).map((pair, i) => {
          const elmt = pair[0].split(" ")[0];
          return (
            <div key={i} className="mb-3 flex justify-between align-center">
              <Text
                variant="h6"
                color={
                  elmt === "Level"
                    ? "lightGold"
                    : elmt === "Physical"
                    ? "White"
                    : elmt
                }
              >
                {pair[0]}
              </Text>
              <Input
                type="text"
                value={pair[1]}
                onChange={(e) => {
                  if (!isNaN(e.target.value)) {
                    setTarget((prev) => {
                      const newTg = deepCopy(prev);
                      newTg[pair[0]] = Math.round(+e.target.value);
                      return newTg;
                    });
                  }
                }}
              />
            </div>
          );
        })}
      </div>
      <Text>
        You can search for your target's Resistances on{" "}
        <Link
          href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
          rel="noreferrer"
          target="_blank"
        >
          this page of the Genshin Impact Wiki
        </Link>
        .
      </Text>
      <div className="mt-2 pr-2">
        <Text variant="h6" color="lightGold">
          Notes:
        </Text>
        {notes.map((pair, i) => (
          <Text key={i} className="mt-1">
            <b>{destructedTypes[pair[0]]}</b> deal{" "}
            <ColoredSpan color={pair[1]}>{pair[1]} DMG</ColoredSpan>.
          </Text>
        ))}
      </div>
    </div>
  );
}
