import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ char, ATTRs, charBCs }) => {
      const { NA, CA, ES } = buffs[0].bnValue(char, ATTRs, charBCs);
      return (
        <>
          Kokomi's{" "}
          <LGreen>Normal Attack, Charged Attack and Bake-Kurage DMG</LGreen> are
          increased based on her <LGreen>Max HP</LGreen>.{" "}
          <Red>
            Normal Attack DMG Bonus (base): {NA}. Charged Attack DMG Bonus
            (base): {CA}. Bake-Kurage DMG Bonus (base): {ES}.
          </Red>
        </>
      );
    },
    isGranted: () => true,
    affect: "self",
    addBnes: ({ ATTRs, bonuses, char, charBCs }) => {
      const { NA, CA, ES } = buffs[0].bnValue(char, ATTRs, charBCs);
      bonuses.NA.Flat += NA;
      bonuses.CA.Flat += CA;
      bonuses.ES.Flat += ES;
    },
    bnValue: (char, ATTRs, charBCs) => {
      const lv = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      const buffMults = {
        NA: 4.84,
        CA: 6.78,
        ES: 7.1
      };
      const result = {};
      for (let type of ["NA", "CA", "ES"]) {
        let mult = buffMults[type] * TalentLvMults[2][lv];
        if (type !== "ES" && charBCs[1].activated) {
          mult += ATTRs["Healing Bonus"] * 0.15;
        }
        result[type] = Math.round(((ATTRs["Base HP"] + ATTRs.HP) * mult) / 100);
      }
      return result;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        During Nereid's Ascension, the{" "}
        <LGreen>Normal and Charged Attack DMG Bonus</LGreen> Kokomi gains based
        on her <LGreen>Max HP</LGreen> will receive a further increase based on{" "}
        <Green>15%</Green> of her <LGreen>Healing Bonus</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        During Nereid's Ascension, Kokomi's <LGreen>Normal Attack SPD</LGreen>{" "}
        is increased by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 10;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        During Nereid's Ascension, Kokomi gains a <Green>40%</Green>{" "}
        <LGreen>Hydro DMG Bonus</LGreen> for 4s after her Normal and Charged
        Attacks heal a character with 80% or more HP.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += 40;
    }
  }
];
