import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { incElementalBnes } from "../../../../helpers/calculators";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, inputs }) => (
      <>
        Provides up to 3 buffs to active characters within the skill's AoE based
        on the number of <ColoredSpan color="Geo">Geo</ColoredSpan> characters
        in the party at the time of casting:
        <br />
        <span className={inputs[0] >= 1 ? "" : "unavailable"}>
           1 Geo character: Adds "Standing Firm" - <LGreen>DEF Bonus</LGreen>
          {toSelf ? (
            "."
          ) : (
            <>
              : <Red>{buffs[0].bnValue(inputs[1])}.</Red>
            </>
          )}
        </span>
        <br />
        <span className={inputs[0] >= 2 ? "" : "unavailable"}>
           2 Geo characters: Adds "Impregnable" - Increased resistance to
          interruption.
        </span>
        <br />
        <span className={inputs[0] >= 3 ? "" : "unavailable"}>
           3 Geo character: Adds "Crunch" - <Green>15%</Green>{" "}
          <LGreen>Geo DMG Bonus</LGreen>
        </span>
      </>
    ),
    isGranted: () => true,
    affect: "party",
    selfLabels: ["Number of Geo characters"],
    labels: ["Number of Geo characters", "Elemental Skill Level"],
    inputs: [1, 1],
    inputTypes: ["select", "text"],
    maxs: [3, 13],
    addBnes: ({ toSelf, ATTRs, char, inputs }) => {
      const { bnValue } = buffs[0];
      ATTRs.DEF += toSelf
        ? bnValue(char["Elemental Skill"] + (char.constellation >= 3 ? 3 : 0))
        : bnValue(inputs[1]);
      if (inputs[0] > 2) ATTRs["Geo DMG Bonus"] += 15;
    },
    bnValue: (level) => {
      return Math.ceil(206 * TalentLvMults[2][level]);
    }
  },
  {
    src: "Ascension 1 Pasive Talent",
    desc: () => (
      <>
        After using Juuga: Forward Unto Victory, all nearby party members'{" "}
        <LGreen>DEF</LGreen> is increased by <Green>25%</Green> for 12s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 25;
    }
  },
  {
    src: "Ascension 4 Pasive Talent",
    desc: () => (
      <>
        Gorou receives the following DMG Bonuses to his attacks based on his
        DEF:
        <br /> <LGreen>Inuzaka All-Round Defense</LGreen>: Skill DMG increased
        by <Green>156%</Green> of <LGreen>DEF</LGreen>
        <br /> <LGreen>Juuga: Forward Unto Victory</LGreen>: Skill DMG and
        Crystal Collapse DMG increased by <Green>15.6%</Green> of{" "}
        <LGreen>DEF</LGreen>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addFinalBnes: ({ bonuses, ATTRs }) => {
      bonuses.ES.Flat += Math.round(ATTRs.DEF * 1.56);
      bonuses.EB.Flat += Math.round(ATTRs.DEF * 0.156);
    }
  },
  {
    src: "Constellation 6",
    desc: ({ inputs }) => (
      <>
        For 12s after using Inuzaka All-Round Defense or Juuga: Forward Unto
        Victory, increases the <LGreen>CRIT DMG</LGreen> of all nearby party
        members' <ColoredSpan color="Geo">Geo DMG</ColoredSpan> based on the
        buff level of the skill's field at the time of use:
        <br />
        <span className={inputs[0] === 1 ? "" : "unavailable"}>
           "Standing Firm": <Green>+10%</Green>
        </span>
        <br />
        <span className={inputs[0] === 2 ? "" : "unavailable"}>
           "Impregnable": <Green>+20%</Green>
        </span>
        <br />
        <span className={inputs[0] === 3 ? "" : "unavailable"}>
           "Crunch": <Green>+40%</Green>
        </span>
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    selfLabels: ["Number of Geo characters"],
    labels: ["Number of Geo characters"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ bonuses, char, inputs, infusion }) => {
      const value = [0, 0.1, 0.2, 0.4][inputs[0]];
      incElementalBnes(char.name, infusion, "Geo", bonuses, "CD", value);
    }
  }
];
