import { useState } from "react";
import { Box, ModalCarpet, ModalInner } from "../../styledCpns/Layout";
import Heading from "./Heading";
import Body from "./Body";
import { useCloseModal } from "../../helpers/hooks";

export default function Picker(props) {
  const { data, dataType } = props;
  const [filter, setFilter] = useState({ type: "", value: "" });
  let filteredData = data;
  if (filter.type.length) {
    filteredData = data.filter((unit) => unit[filter.type] === filter.value);
  }
  const ref = useCloseModal(props.close);

  return (
    <ModalCarpet className="not-center-y">
      <ModalInner ref={ref}>
        <Box height="10%" className="p-2">
          <Heading
            dataType={dataType}
            close={props.close}
            filter={filter}
            setFilter={(chosen, newFilter) =>
              setFilter(chosen ? { type: "", value: "" } : newFilter)
            }
          />
        </Box>
        <Box height="90%" className="px-3 pt-2 pb-3">
          <Body
            data={filteredData}
            forChar={dataType === "Character"}
            pick={props.pick}
          />
        </Box>
      </ModalInner>
    </ModalCarpet>
  );
}
